<template>
	<transition name="slide">
		<music-list :rank="rank" :songs="songs" :title="title" :bg-image="bgImage"></music-list>
	</transition>
</template>

<script>
	import MusicList from '../music-list/music-list'
	import {mapGetters} from 'vuex'
	import {getMusicList} from "api/rank";
	import {ERR_OK} from 'api/config'
	import {createSong} from 'common/js/song'

	export default {
			data(){
				return {
					songs:[],
					rank:true
				}
			},
			computed:{
				title() {
					return this.topList.topTitle
				},
				bgImage() {
					if(this.songs.length){
						return this.songs[0].image
					}
					return ''
				},
				...mapGetters([
					'topList'
				])
			},
			created() {
				//console.log(this.topList.id)
				this._getMusicList(this.topList.id)
			},
			methods:{
				_getMusicList(topid){
					getMusicList(topid).then((res)=>{
						//console.log(res.code)
						if (res.code === ERR_OK) {
							this.songs = this._normalizeSongs(res.songlist)
							//console.log(this._normalizeSongs(res.songlist))
						}
					})
				},
				_normalizeSongs(list) {
					let ret = []
					list.forEach((item) =>{
						const musicData = item.data
						if(musicData.albummid && musicData.songid) {
								ret.push(createSong(musicData))
							}
					})
					return ret
				},
			},
			components:{
				MusicList
			}
	}
</script>

<style scoped lang="stylus">
	.slide-enter-active,.slide-leave-active
		transition:all 0.3s ease
		
	.slide-enter,.slide-leave-to
		transform:translate3d(100%,0,0)
</style>


